import React from 'react';
import styled from 'styled-components';

type UnityGameProps = {
  src: string;
  title: string;
  height?: number;
  width?: number;
};

// Define the component
const UnityGame: React.FC<UnityGameProps> = ({ src, title, width = 1120, height = 680 }) => {
  return (
    <GameContainer aspectRatio={height / width}>
      <GameIframe src={src} title={title} allowFullScreen={true} />
    </GameContainer>
  );
};

export default UnityGame;

// Define the prop types for styled components
type GameContainerProps = {
  aspectRatio: number;
};

// Styled Component for the container that handles aspect ratio
const GameContainer = styled.div<GameContainerProps>`
  position: relative;
  width: 100%;
  padding-top: ${({ aspectRatio }) => `${aspectRatio * 100}%`}; /* Dynamically calculate the padding-top */
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
`;

// Styled Component for the iframe
const GameIframe = styled.iframe`
    border: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; /* Scale the iframe to fill the container width */
    height: 100%; /* Scale the iframe to fill the container height */
    object-fit: contain; /* Ensure the game scales to fit without being cut off */
`;
