import React from 'react';
import Project from "./Project";
import styled from 'styled-components';

const E9: React.FC = () => {
  return (
    <Project
      thumbnail="/E9/thumbnail.png"
      thumbnailGif="/E9/preview.gif"
      title="E9"
      date={"2021-current"}
      tech={["C#", "Typescript", "Unity", "ThreeJS", "React", "Postgres", "Docker"]}
      platforms={["Web", "Oculus Quest 2"]}
      description="An application that maps data to 3D visualizations that can be viewed in VR."
      contributions={"I developed procedurally generated 3D meshes, custom parent-child relationships, a glyph editor, as well as a VR rig to view the data in VR."}
      section="VR Projects"
    >
      {/*<StyledImage src="/E9/preview.gif" alt="E9"/>*/}
      {/*<h1>Procedural Mesh Generation</h1>*/}
      {/*<StyledImage src="/E9/3DPieChart.png" alt="E9"/>*/}
      {/*<StyledImage src="/E9/Pie.gif" alt="E9"/>*/}
      {/*<h1> UI</h1>*/}
      {/*<StyledImage src="/E9/UI.png" alt="E9"/>*/}
      {/*<StyledImage src="/E9/UI2.png" alt="E9"/>*/}
      {/*<StyledImage src="/E9/UI3.png" alt="E9"/>*/}
      {/*<h1>Jupyter/Polyglot integration</h1>*/}
      {/*<StyledImage src="/E9/Editor2.png" alt="E9"/>*/}
      {/*<StyledImage src="/E9/Editor3.png" alt="E9"/>*/}
      {/*<h1>Injecting Fun where ever possible</h1>*/}
      {/*<h2>Messing with vertices shaders to create dancing glyphs </h2>*/}
      {/*<StyledImage src="/E9/DancingGlyphs.gif" alt="E9"/>*/}
      {/*<h2>Exploding Glyphs</h2>*/}
      {/*<StyledImage src="/E9/ExplodingGlyphs.gif" alt="E9"/>*/}
      <p>Due patent concerns, I can't show much content.</p>
    </Project>
  );
};

export default E9;

// Styled component for uniform image size
const StyledImage = styled.img`
  width: auto;   // Set a fixed width for all images
  height: 300px;   // Maintain aspect ratio by setting height to 'auto'
  object-fit: cover; // Ensure images cover the defined area without stretching
  border-radius: 5px;
  margin-bottom: 1rem; // Optional: Adds some spacing between images and text
`;
