import React from 'react';
import styled from 'styled-components';
import { graphql, useStaticQuery } from "gatsby";
import Bubble from "../reusable/Bubble";
import E9 from "./E9";
import ZeldaClone from "./ZeldaClone";
import BO6 from "./B06";
import MathCommand from "./MathCommand";
import BiggestFishInThePond from "./BiggestFishInThePond";
import HobbitSays from "./HobbitSays";
import Arduino from "./Arduino";
import GraphWars from "./GraphWars";
import EcoSystemSimulator from "./EcoSystemSimulator";
import KikisCantina from "./KikisCantina";
import Portfolio from "./Portfolio";
import MergyDergy from "./MergyDergy";

const ProjectsSection: React.FC = () => {
  const data = useStaticQuery(graphql` query { site { siteMetadata { bannerLogo } } } `);

  if (!data) return <div>Failed to load site metadata</div>;

  return (
    <Bubble maxWidth={"auto"}>
      <SectionStyled id={"projects"}>
        <h2>Games/Projects</h2>
        <CategorySection>
          <h3>Featured</h3>
          <ProjectsGrid>
            <BO6/>
            <KikisCantina/>
            <E9></E9>
          </ProjectsGrid>
        </CategorySection>
        <CategorySection>
          <h3>3D Projects</h3>
          <ProjectsGrid>
            <EcoSystemSimulator/>
          </ProjectsGrid>
        </CategorySection>
        <CategorySection>
          <h3>2D Projects</h3>
          <ProjectsGrid>
            <GraphWars/>
            <MergyDergy/>
            <MathCommand/>
            <ZeldaClone/>
            <BiggestFishInThePond/>
            <HobbitSays/>
          </ProjectsGrid>
        </CategorySection>
        <CategorySection>
          <h3>Software/Non-game Projects</h3>
          <ProjectsGrid>
            <Portfolio/>
          </ProjectsGrid>
        </CategorySection>
        <CategorySection>
          <h3>Experiments</h3>
          <ProjectsGrid>
            <Arduino/>
          </ProjectsGrid>
        </CategorySection>
      </SectionStyled>
    </Bubble>
  );
};

export default ProjectsSection;

export const SectionStyled = styled.section`
    padding: 1rem;

    h2 {
        color: red;
    }
`;

const CategorySection = styled.div`
    margin-bottom: 3rem; 

    h3 {
        color: #ffffff;
        text-shadow: 1px 1px 2px #000000;
        margin-bottom: 1rem;
    }
`;

const ProjectsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
`;
