import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const HobbitSays: React.FC = () => {
  return (
    <Project
      thumbnail={"/Arduino/thumbnail.png"}
      thumbnailGif="/Arduino/thumbnail.png"
      title="Arduino Unity Integration"
      tech={["C#", "Unity", "Arduino"]}
      date={"2021"}
      platforms={["None"]}
      description="An experiment to see if I could get Unity to communicate with an Arduino and vice versa."
      contributions={"I developed a custom serial communication protocol and a Unity plugin to interface with the Arduino."}
      section="Experiments"
    >
      <iframe 
        src="https://www.linkedin.com/embed/feed/update/urn:li:ugcPost:6911095478105378816" 
        height="1093"
        width="504" 
        frameBorder="0" 
        title="Embedded post"/>
    </Project>
);
}

export default HobbitSays;