import React from 'react';
import styled from 'styled-components';
import Project from "./Project";

// Styled components for better layout and design
const FeatureContainer = styled.div`
    margin-top: 20px;
`;

const FeatureList = styled.ul`
    list-style-type: none;
    padding: 0;
`;

const FeatureItem = styled.li`
    margin-bottom: 20px;
`;

const FeatureTitle = styled.h4`
    margin-bottom: 5px;
    color: #ffffff;
    text-decoration: underline;
`;

const FeatureImage = styled.img`
    max-width: 100%;
    margin-top: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
`;

const SectionHeader = styled.h3`
    color: #9c27b0; /* Match heading color with active tab color */
    text-shadow: 1px 1px 1px #000000;
  `;

const KikisCantina: React.FC = () => {
  const tools = [
    {name: 'Custom Editors', image: ['/KikisCantina/CustomInspector.gif']},
    {name: 'Repeatable Tile Generator', image: ['/KikisCantina/RepeatTile.gif']},
    {name: 'Rule Tile from 2 textures', image: ['/KikisCantina/RuleTile.gif']},
    {name: 'Various Texture Editors', image: [
      '/KikisCantina/Shadow.gif',
      '/KikisCantina/Overlay.gif',
      '/KikisCantina/Mirror.gif',
      '/KikisCantina/opacity.gif',
      '/KikisCantina/Rotate.gif',
      ]},
  ];

  const gameplayFeatures = [
    {feature: 'Farming/Cooking/Automation', image: '/KikisCantina/Gameplay.gif', description: 'Grow crops, cook meals, and automate your farm.'},
    {feature: 'Automation Drones', image: '/KikisCantina/Drones.gif', description: 'Automate your farm with drones.'},
    {feature: 'Contextual UI Hints', image: '/KikisCantina/ContextHints.gif', description: 'UI hints that simplify interacting with game devices such as item processors.'},
    {feature: 'Quality of life features', image: '/KikisCantina/FarmingQOL.gif', description: 'Context-based tool use, easy inventory management, and more.'},
  ];

  return (
    <Project
      thumbnail={"/KikisCantina/thumbnail.png"}
      thumbnailGif="/KikisCantina/preview.gif"
      title="Kiki's Cantina"
      date={"2024"}
      tech={["C#", "Unity", "NUnit"]}
      platforms={["PC"]}
      description="A 2D cosy farming/cooking game with automation elements. Designed to be a cozy game with opt in stress mechanics."
      contributions={"I created the game from scratch."}
      section="2D Projects"
    >
      <FeatureContainer>
        <SectionHeader>Tools Developed:</SectionHeader>
        <FeatureList>
          {tools.map((tool, index) => (
            <FeatureItem key={index}>
              <FeatureTitle>{tool.name}</FeatureTitle>
              {tool.image.map((image, index) => (
                <FeatureImage src={image} />
              ))}
            </FeatureItem>
          ))}
        </FeatureList>
  
        <SectionHeader>Gameplay Features:</SectionHeader>
        <FeatureList>
          {gameplayFeatures.map((feature, index) => (
            <FeatureItem key={index}>
              <FeatureTitle>{feature.feature}</FeatureTitle>
              <FeatureImage src={feature.image} alt={feature.feature} />
              <p>{feature.description}</p>
            </FeatureItem>
          ))}
        </FeatureList>
      </FeatureContainer>
      <FeatureContainer>
        <SectionHeader>Project/Architecture Considerations:</SectionHeader>
        <ul>
          <li>Scriptable Objects for data-driven design</li>
          <li>Custom Editors for easy content creation</li>
          <li>Rigorously tested with NUnit</li>
          <li>
            <div>
              <p>Tile-based Implementation.</p>
              <img src="/KikisCantina/Automation.gif"></img>
            </div>
          </li>
          <li>
            <div>
              <p>Flexible Visual Representation for easy adaptations to 3D, 2.5D, tile/non-tile-based 2D, etc.</p>
              <img src="/KikisCantina/VisualCode.png" width={1200} height={800}></img>
            </div>
          </li>
          <li>
            <div>
              <p>Performant techniques such as object pooling and combining ground items.</p>
              <img src="/KikisCantina/Performance.gif"></img>
              <img src="/KikisCantina/GroundItems.gif"></img>
            </div>
          </li>

        </ul>
      </FeatureContainer>
    </Project>
  );
};

export default KikisCantina;