import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const BiggestFishInThePond: React.FC = () => {
  return (
    <Project
      thumbnail="/Biggest Fish In The Pond/thumbnail.png"
      thumbnailGif="/Biggest Fish In The Pond/preview.gif"
      title="Biggests Fish in The Pond"
      date={"2021"}
      tech={["C#", "Unity"]}
      platforms={["Android", "Web"]}
      description="A simple feeding frenzy clone where you play as a fish eating smaller fish to grow larger. Features an upgrade system ad integration with watch rewards (Not featured in demo). Briefly available on the google play store."
      contributions={"I developed everything from scratch."}
      playable={true}
      section="2D Projects"
    >
      <p>Controls: Click or hold click tell your fish where to move to.</p>
      <UnityGame src={"/Biggest Fish In The Pond/index.html"} title={"Biggest Fish In The Pond"}/>
    </Project>
  );
}

export default BiggestFishInThePond;