import React, { useState } from 'react';
import Styled from 'styled-components';
import ProjectPopup from './ProjectPopup';

type ProjectProps = {
  thumbnail: string;
  thumbnailGif: string;
  title: string;
  date: string;
  tech: string[];
  platforms: string[];
  description: string;
  section: string;
  contributions: string;
  playable?: boolean;
  offset?: string;
  children?: React.ReactNode;
};

const Project: React.FC<ProjectProps> = ({
                                           thumbnail,
                                           thumbnailGif,
                                           title,
                                           date,
                                           tech,
                                           platforms,
                                           description,
                                           contributions,
                                           section,
                                           playable = false,
                                           offset = 'center',
                                           children,
                                         }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  const handleMouseEvents = (set: boolean) => () => setIsHovered(set);
  const handlePopup = (set: boolean, action: string) => () => {
    if (typeof window !== "undefined" && typeof window.gtag === "function") {
      window.gtag('event', action, {
        event_category: 'Project Interaction',
        event_label: title,
        event_section: section,
        transport_type: 'beacon',
      });
      for (let i = 0; i < tech.length; i++) {
        window.gtag('event', action, {
          event_category: 'Project Interaction',
          event_label: tech[i],
          transport_type: 'beacon',
        });
      }
      for (let i = 0; i < platforms.length; i++) {
        window.gtag('event', action, {
          event_category: 'Project Interaction',
          event_label: platforms[i],
          transport_type: 'beacon',
        });
      }
    }
    setIsOpen(set);
    setIsHovered(false);
  };

  const projectStyles: React.CSSProperties = {
    width: 'fit-content',
    minHeight: '400px',
    padding: '1rem',
    border: playable ? '2px solid red' : '1px solid #ccc', // Red border if playable is true
    borderRadius: '5px',
    cursor: 'pointer',
    boxShadow: playable ? '0 0 10px red' : 'none', // Optional: red glow effect for playable
  };

  const thumbnailStyles: React.CSSProperties = {
    width: '300px',
    height: '200px',
    objectFit: 'cover',
    borderRadius: '5px',
    objectPosition: offset, // Use the offset prop here to control thumbnail positioning
  };
  
  const StyledText= Styled.p`
    color: #666;
    margin-bottom: 10px;
    width: 80%;
  `;

  return (
    <div style={projectStyles} onMouseEnter={handleMouseEvents(true)} onMouseLeave={handleMouseEvents(false)} onClick={handlePopup(true, 'Open Project')}>
      <ProjectPopup
        isOpen={isOpen}
        handleClose={handlePopup(false, 'Close Project')}
        title={title}
        date={date}
        technologies={tech.join(', ')}
        platform={platforms.join(', ')}
        description={description}
        contributions={contributions}
      >
        {children}
      </ProjectPopup>
      <img src={isHovered ? thumbnailGif : thumbnail} alt={title} style={thumbnailStyles} />
      <h3 style={{ marginTop: '10px' }}>
        {title} {playable && <span style={{ color: 'red' }}> (Playable)</span>} {/* Add playable indicator */}
      </h3>
      <StyledText>{date}</StyledText>
      <StyledText>{tech.join(', ')}</StyledText>
      <StyledText>{description}</StyledText>
    </div>
  );
};

export default Project;
