import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const HobbitSays: React.FC = () => {
  return (
    <Project
      thumbnail="/Hobbit Says/thumbnail.png"
      thumbnailGif="/Hobbit Says/preview.gif"
      title="Hobbit Says"
      tech={["C#", "Unity"]}
      date={"2021"}
      platforms={["Android", "Web"]}
      description="A simple simon says game. Features ad integration (Not featured in demo). Briefly available on the google play store."
      contributions={"I developed everything from scratch."}
      playable={true}
      section="2D Projects"
    >
      <UnityGame src={"/Hobbit Says/index.html"} title={"Hobbit Says"} width={375} height={600}/>
    </Project>
  );
}

export default HobbitSays;