import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const EcoSystemSimulator: React.FC = () => {
  return (
    <Project
      thumbnail="/EcoSystem/thumbnail.png"
      thumbnailGif="/EcoSystem/preview.gif"
      title="Eco System Simulator"
      tech={["C#", "Unity"]}
      date={"2020"}
      platforms={["PC", "Web"]}
      description="A simple ecosystem simulator where the goal is to find the right balance between predators and prey. Creatures have genes that mutate and are passed on to offspring."
      contributions={"I developed everything from scratch."}
      playable={true}
      section="3D Projects"
    >
      <p><b>Controls:</b></p>
      <p>W, A, S, D to move</p>
      <p>E, Q, to move up and down</p>
      <p>Left click on specimen to follow and see stats</p>
      <p>Tab while following a specimen to switch view</p>
      <p>Scroll wheel while following a specimen to zoom in and out</p>
      <p>Right click to stop following a specimen</p>
      <p>Hold Space to see specimen's detection range</p>
      <p>Hold Left Alt while following specimen to see them from the front</p>
      <UnityGame src={"/EcoSystem/index.html"} title={"Eco System Simulator"}/>
    </Project>
  );
}

export default EcoSystemSimulator;