import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const MergyDergy: React.FC = () => {
  return (
    <Project
      thumbnail="/MergyDergy/thumbnail.png"
      thumbnailGif="/MergyDergy/preview.gif"
      title="Mergy Dergy"
      tech={["C#", "Unity"]}
      date={"2024"}
      platforms={["PC", "Web", "Android"]}
      description="A Suika game clone with some QOL enhancements, play advocacy features, and some alternate physics."
      contributions={"I developed everything from scratch."}
      playable={true}
      section="2D Projects"
    >
      <p><b>Goal:</b></p>
      <p>Merge 2 level 10 circle or score higher points than your friends!</p>
      <p>Get same level circles to touch to merge them into a bigger circle.</p>
      <p><b>Controls:</b></p>
      <p>Left Click to drop</p>
      <p>Right Click to Aim</p>

      <UnityGame src={"/MergyDergy/index.html"} title={"Mergy Dergy"}/>
    </Project>
  );
}

export default MergyDergy;