import React from 'react';
import UnityGame from "./UnityGame";
import Project from "./Project";

const MathCommand: React.FC = () => {
  return (
    <Project
      thumbnail="/Math Command/thumbnail.png"
      thumbnailGif="/Math Command/preview.gif"
      title="Math Command"
      date={"2020"}
      tech={["C#", "Unity"]}
      platforms={["Web"]}
      description="Educational Math game based on Missle Command/Space Invaders where you shoot down enemy ships by solving math problems."
      contributions={"I developed quality of life improvements such as difficulty settings and target selection. I produced all art, music and sfx."}
      section="2D Projects"
      playable={true}
      offset={"bottom"}
    >
      <p>Controls: Type a matching letter to select a target. Enter the answer and hit return.</p>
      <UnityGame src={"/Math Command/index.html"} title={"Math Command"}/>
    </Project>
  );
}

export default MathCommand;