import React from 'react';
import Project from "./Project";

const GraphWars: React.FC = () => {
  return (
    <Project
      thumbnail={"/GraphWars/thumbnail.png"}
      thumbnailGif="/GraphWars/preview.gif"
      title="Graph Wars"
      date={"2024"}
      tech={["Python", "Tkinter", "Flask", "RDF"]}
      platforms={["Web"]}
      description="A game based on Risk, where players interact with the game by writing scripts that take turns executing. Features script sandboxing and WIP flask API for agnostic clients."
      contributions={"I created the game from scratch, including the game engine, the AI, and the web interface."}
      section="2D Projects"
    >
      <img src="/GraphWars/preview.gif" alt="Graph Wars 1" />
      <h1>Example Script</h1>
      <img src="/GraphWars/Script.png" alt="Graph Wars 2" />
    </Project>
  );
};

export default GraphWars;