import React from 'react';
import Project from "./Project";

const Portfolio: React.FC = () => {
  return (
    <Project
      thumbnail={"/HobbitCraftLogo.png"}
      thumbnailGif="/HobbitCraftLogo.png"
      title="Portfolio"
      date="2024"
      tech={["Javascript", "Typescript", "CSS", "HTML", "Gatsby", "React"]}
      platforms={["Web"]}
      description="A portfolio website showcasing my projects and skills."
      contributions={"I developed everything from scratch."}
      section="Software/Non-game Projects"
    >
    </Project>
  );
};

export default Portfolio;